import React, { useState, useContext } from 'react';
import { Button, TextField, Grid, Typography, Container, Paper } from '@material-ui/core';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { Assignment, Phone, PhoneDisabled } from '@material-ui/icons';
import { makeStyles } from '@material-ui/core/styles';
import './style.scss';
import { SocketContext } from '../Context';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
  },
  gridContainer: {
    width: '100%',
    [theme.breakpoints.down('xs')]: {
      flexDirection: 'column',
    },
  },
  container: {
    width: '600px',
    margin: '35px 0',
    padding: 0,
    [theme.breakpoints.down('xs')]: {
      width: '80%',
    },
  },
  margin: {
    marginTop: 20,
  },
  padding: {
    padding: 20,
  },
  paper: {
    padding: '10px 20px',
    border: '2px solid black',
  },
}));

const Sidebar = ({ children }) => {
  const { me,
    callAccepted,
    name,
    setName,
    callEnded,
    leaveCall,
    callUser,
    streamVoice,
    streamWebcam,
    streamScreen
  } = useContext(SocketContext);
  const [idToCall, setIdToCall] = useState('');
  const classes = useStyles();

  return (
    <Container className={classes.container}>
      <div id={"button-container"}>
        <Button
            variant="contained"
            color="secondary"
            startIcon={<PhoneDisabled fontSize="large" />}
            onClick={streamVoice}
            id={"audio-button"}
            className={classes.margin}
        >
          Audio
        </Button>
        <Button variant="contained" color="secondary" startIcon={<PhoneDisabled fontSize="large" />}
                onClick={streamWebcam} className={classes.margin}>
          Webcam
        </Button>
        <Button variant="contained" color="secondary" startIcon={<PhoneDisabled fontSize="large" />}
                onClick={streamScreen} className={classes.margin}>
          Share Screen
        </Button>
      </div>
      <Paper elevation={10} className={classes.paper}>
        <form className={classes.root} noValidate autoComplete="off">
          <Grid container className={classes.gridContainer}>
            <Grid item xs={12} md={6} className={classes.padding}>
              <Typography gutterBottom variant="h6" id={"text"}>اطلاعات کاربر</Typography>
              <TextField
                  id={"text"}
                  label="نام" value={name} onChange={(e) => setName(e.target.value)} fullWidth />
              <CopyToClipboard text={me} className={classes.margin}>
                <Button
                    id={"text"}
                    variant="contained" color="primary" fullWidth startIcon={<Assignment fontSize="large" />}>
                  کپی کردن ID
                </Button>
              </CopyToClipboard>
            </Grid>
            <Grid item xs={12} md={6} className={classes.padding}>
              <Typography id={"text"} gutterBottom variant="h6">برقراری تماس</Typography>
              <TextField
                  label="برای تماس ID" value={idToCall}
                  id={"text"}
                  onChange={(e) => setIdToCall(e.target.value)} fullWidth />
              {callAccepted && !callEnded ? (
                <Button
                    variant="contained"
                    id={"text"}
                    color="secondary" startIcon={<PhoneDisabled fontSize="large" />} fullWidth onClick={leaveCall} className={classes.margin}>
                  قطع تماس
                </Button>
              ) : (
                <Button
                    id={"text"}
                    variant="contained" color="primary" startIcon={<Phone fontSize="large" />} fullWidth onClick={() => callUser(idToCall)} className={classes.margin}>
                  برقراری تماس
                </Button>
              )}
            </Grid>
          </Grid>
        </form>
        {children}
      </Paper>
    </Container>
  );
};

export default Sidebar;
